/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

const ambLight= new THREE.AmbientLight( 0xffffff, 1.1 ); // soft white light
scene.add(ambLight);

const lightMaterial = new THREE.MeshBasicMaterial({color: 0xffbb00})



/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(50, sizes.width / sizes.height, 0.1, 30000)
camera.position.set(60,30,70)
camera.lookAt(-1,20,0)
scene.add(camera)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true,
    antialias: true
})
renderer.setClearColor(0x000000, 0);
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

let mixer = null
let mixer2 = null
let actions;
let rx7Action;

// gltfLoader.load('toyota_ae86_scene - Copy/ae86.gltf', function (gltf){
gltfLoader.load('ae2/ae86.gltf', function (gltf){
    console.log(gltf)
    scene.add(gltf.scene);
    mixer = new THREE.AnimationMixer(gltf.scene);
    mixer2 = new THREE.AnimationMixer(gltf.scene);
    rx7Action = mixer.clipAction(gltf.animations[12]);
    for(let i = 0; i <= 18 ; i++){
        const action = mixer.clipAction(gltf.animations[i])
        action.play()
        action.setEffectiveTimeScale(1)
        if(i==11 || i==12){
            action.play()
            action.setEffectiveTimeScale(0.4)
        }
    }

    actions = [rx7Action];

    tick();
    // const action = mixer.clipAction(gltf.animations[0])
    // action.play()
    // const action2 = mixer.clipAction(gltf.animations[5])
    // action2.play()
    // action2.setEffectiveTimeScale(0.2)
    // for(let i = 10; i < 14; i++){
    //     gltf.scene.children[0].children[i].material = lightMaterial
    //     // gltf.scene.children[0].children[i].layers.toggle(BLOOM_SCENE)
    // }
    
    // gltf.scene.children[9].children[4].layers.toggle(BLOOM_SCENE)
    // gltf.scene.children[9].children[5].material = lightMaterial
    // for(let i = 0; i < 12; i++){
    //     gltf.scene.children[9].children[5].children[i].material = lightMaterial
    //     // gltf.scene.children[9].children[4].children[i].layers.toggle(BLOOM_SCENE)
    // }
 
    
   
})

//COMPOSER
// var parameters = { minFilter: THREE.LinearFilter, magFilter: THREE.LinearFilter, format: THREE.RGBAFormat, stencilBuffer: true };

// var renderTarget = new THREE.WebGLRenderTarget( sizes.width, sizes.height, parameters );
var composer = new EffectComposer(renderer);
var renderPass = new RenderPass(scene,camera);
// renderPass.clearColor = new THREE.Color(0, 0, 0);
// renderPass.clearAlpha = 0;
composer.addPass(renderPass);


// var ssrPass = new SSRPass();
// composer.addPass(ssrPass);

//custom shader pass
var vertShader = document.getElementById('vertexShader').textContent;
var fragShader = document.getElementById('fragmentShader').textContent;
var counter = 0.0;
var myEffect = {
  uniforms: {
    "tDiffuse": { value: null },
    "amount": { value: counter }
  },
  vertexShader: vertShader,
  fragmentShader: fragShader
}

var customPass = new ShaderPass(myEffect);
customPass.renderToScreen = true;
composer.addPass(customPass);

const rgbShiftPass = new ShaderPass(RGBShiftShader);
rgbShiftPass.uniforms['amount'].value = 1.2/sizes.width;
// rgbShiftPass.uniforms['resolution'].value(1/window.innerWidth,1/window.innerHeight)

var cPass = new ShaderPass(ACESFilmicToneMappingShader);
cPass.renderToScreen = true
cPass.material.transparent = true;
composer.addPass(cPass);



var fxaaPass = new ShaderPass(FXAAShader);
fxaaPass.uniforms.resolution.value.set(1/window.innerWidth,1/window.innerHeight);
// fxaaPass.renderToScreen = true;
// fxaaPass.material.transparent = true; // FIX
composer.addPass(fxaaPass);


composer.addPass(rgbShiftPass);
// composer.addPass(gammaCorrectionPass)


function unPauseAllActions() {

    // actions.forEach( function ( action ) {

    //     action.paused = true;

    // } );

}

let singleStepMode = false;
let sizeOfNextStep = 0;

function toSingleStepMode() {

    unPauseAllActions();
    console.log("hi")
    singleStepMode = true;
    sizeOfNextStep = 1;

}
/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0;
const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    if( mixer !== null){
        mixer.update(deltaTime)
    }

    // Render
    // renderer.render(scene, camera)

    counter += 0.0000005;
    customPass.uniforms["amount"].value = counter;

    let mixerUpdateDelta = clock.getDelta();

    if ( singleStepMode ) {

        mixerUpdateDelta = sizeOfNextStep;
        sizeOfNextStep = 0;

    }

    if( mixer2 !== null){
        mixer2.update(mixerUpdateDelta)
    }

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
    composer.render();
}


gsap.to(camera,{
    scrollTrigger: {
        onUpdate: toSingleStepMode(),
    }
})